<template>
  <div class="container-fluid m-0 p-0">
    <div id="home-page" class="full-height p-4">
        <div class="container pt-3">
            <div class="row pb-5">
                <div class="col-lg-6 pt-4 my-auto">
                <div class="text-center mb-3 d-block d-lg-none">
                        <h1 class="display-3"><font color="white">{{title}}</font></h1>
                        <h3><font color="white">Find the World</font></h3>
                    </div>
                    <img id="phone-img" src="../assets/travel-smartphone.png" alt="">
                </div>
                <div class="col-lg-6 my-auto pt-4">
                    <div class="text-center mb-3 d-none d-lg-block">
                        <h1 class="display-3"><font color="white">{{title}}</font></h1>
                        <h3><font color="white">Find the World</font></h3>
                    </div>
                    <div class="border p-3 p-md-5 bg-white rounded shadow">
                        <h2>Coming Soon!</h2>
                        <form @submit.prevent="addEmail(email)">
                            <div class="form-group">
                                <label for="emailSignup">Reserve your account now, we'll contact you when it's live</label>
                                <input v-model="email" id="emailSignup" type="email" placeholder="Enter your Email" class="form-control">
                                <small id="emailHelp" class="form-text text-mute">We'll never share your email address</small>
                                <button type="submit" class="btn btn-success mt-3">Join Waiting List</button>
                                <div class="mt-4">
                                    <p class="m-0">{{ message }}</p>
                                </div>
                            </div>
                        </form>
                    </div>
                    <div id="available" class="p-2">
                        <h4 class="text-center mb-1 mt-5 text-light">Available On</h4>
                        <div class="row">
                            <div class="col my-auto">
                                <img src="../assets/app_store.png" alt="app_store" class="float-right img-fluid app_store_img">
                            </div>
                            <div class="col my-auto">
                                <img src="../assets/googleplay.png" alt="app_store" class="float-left img-fluid app_store_img">
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
    </div>

    <div id="about" class="bg-light p-3 p-md-5">
        <div class="row">

            <div class="card col-lg-4">
                <div class="col-lg col-md-auto col-sm-auto">
                    <img src="../assets/icons/plan.svg" alt="plan" class="m-4" width="75">
                </div>
                <div>
                <h5 class="ml-md-4 mr-md-4">
                <strong>"Plan"</strong><br>
                Lorem, ipsum dolor sit amet consectetur adipisicing elit. Aut officiis officia esse numquam deserunt iste aspernatur, 
                voluptatibus nemo omnis aliquid suscipit doloremque eligendi! Obcaecati reprehenderit provident quam asperiores id voluptates!
                </h5>
                </div>
            </div>

            <div class="card col-lg-4">
                <div class="col-lg col-md-auto col-sm-auto">
                    <img src="../assets/icons/wallet.svg" alt="plan" class="m-4" width="75">
                </div>
                <div>
                <h5 class="ml-md-4 mr-md-4">
                <strong>"Budget"</strong><br>
                Lorem, ipsum dolor sit amet consectetur adipisicing elit. Aut officiis officia esse numquam deserunt iste aspernatur, 
                voluptatibus nemo omnis aliquid suscipit doloremque eligendi! Obcaecati reprehenderit provident quam asperiores id voluptates!
                </h5>
                </div>
            </div>

            <div class="card col-lg-4">
                <div class="col-lg col-md-auto col-sm-auto">
                    <img src="../assets/icons/passport.svg" alt="plan" class="m-4" width="75">
                </div>
                <div>
                <h5 class="ml-md-4 mr-md-4">
                <strong>"Expense"</strong><br>
                Lorem, ipsum dolor sit amet consectetur adipisicing elit. Aut officiis officia esse numquam deserunt iste aspernatur, 
                voluptatibus nemo omnis aliquid suscipit doloremque eligendi! Obcaecati reprehenderit provident quam asperiores id voluptates!
                </h5>
                </div>
            </div>

        </div>
    </div>

    <div id="contact" class="p-4">
        <div class="row justify-content-center mt-3 mb-3">

            <div v-if="show_contact == true" class="col-lg-4">
                <h2><font color="white">Have Any Questions?</font></h2>
                <small><font color="white">Contact us by filling out the information below.</font></small>

                <div v-if="contact_notice != ''" class="alert alert-warning">
                    There was a problem with your submitting message. {{contact_notice}}
                </div>

                <form @submit.prevent="sendContactMessage()">
                    <div class="form-group text-left">
                        <input v-model="contact_email" type="email" class="form-control" placeholder="Enter your Email">
                        <textarea v-model="contact_message" class="form-control mt-3" name="text" cols="30" rows="5" placeholder="Enter your Message"></textarea>
                    </div>
                    <button type="submit" class="btn btn-primary">Send Message</button>
                </form>
            </div>
            <div v-else>
                <h3>Message Has Been Sent Successfully!</h3>
                <p>Thank you for contacting us, we'll get back to you as soon as we can.</p>
            </div>
        </div>
    </div>

    <div id="footer" class="p-4">
        <footer class="text-light p-4">
            <a href="#" target="_blank" class="text-light">Free Nation of the World S.R.L</a> |
            <a href="#" class="text-light">Privacy Policy</a>
            <br>
            <small>&copy; 2020 Travering app</small>
        </footer>
    </div>

  </div>
</template>

<script>
import { Auth } from '@/firebase/auth.js'

export default {
  data () {
    return {
        title: 'Travel Treasury',
        email: '',
        message: '',
        show_contact: true,
        contact_email: '',
        contact_message: '',
        contact_notice: '',
    }
  },
  methods: {
        async addEmail(email){
            var noticeMessage = "Your account has been reserved!"
            await Auth.createUserWithEmailAndPassword(email, this.randomPassword(20)).catch(function(error){
                if (error.code != "auth/email-already-in-use") {
                    noticeMessage = error.message;
                }
            });
            this.message = noticeMessage;
            this.email = '';
        },
        randomPassword (length){
        var chars = "abcdefghijcklmnopqrstuvwxyz!@#$%^&*()-+<>ABCDEFGHIJKLMNOPQRSTUVWXYZ1234567890";
        var password = "";
        for (var x = 0; x < length; x++) {
            var i = Math.floor(Math.random() * chars,length );
            password += chars.charAt(i);
            }
            return password;
        },
        sendContactMessage(){
            // if (!this.validEmail(this.contact_email)){
            //     contact_notice = 'The email address is badly formatted.';
            // }else if(this.contact_message.length < 10){
            //     this.contact_notice = "Your message is to short.";
            // }else{
            // const url = `https://us-central1-travel-budget-eefcd.cloudfunctions.net/sendEmail?email_from=${this.contact_email}&message=${this.contact_message}`
            // const requestOptions = {
            // method: "GET",
            // headers: { "Content-Type": "application/json" }
            // };
            // fetch(url, requestOptions);
            // this.show_contact = false;
      
        },
        validEmail(email){
            var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return re.test(email);
        }
    
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #home-page{
        background-color: #9202f2;
        background: url('../assets/travel.jpg') no-repeat center center;
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
    }
    #about{
        min-height: 40vh;
    }
    #phone-img{
        max-height: 80vh;
    }
    #contact{
        background-color: #57aeaf;
    }
    #footer{
        background-color: #252223;
    }
    .app_store_img{
        max-height: 250px;
    }
</style>



